.fdcp-badge {
  position: relative;
  display: inline-flex;
  padding: 0.25rem 0.75rem;
  border-radius: 1.875rem;
  white-space: nowrap;
  font-size: 0.875rem;
  line-height: 1.25rem;
  font-weight: 500;
  vertical-align: middle;
  gap: 0.25rem;
}
.fdcp-badge .fdcp-badge-start,
.fdcp-badge .fdcp-badge-end {
  display: inline-flex;
  align-items: center;
  justify-content: center;
}
.fdcp-badge .fdcp-badge-start > *,
.fdcp-badge .fdcp-badge-end > * {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: inherit;
  line-height: inherit;
}
.fdcp-badge .fdcp-badge-start i, .fdcp-badge .fdcp-badge-start svg, .fdcp-badge .fdcp-badge-start img,
.fdcp-badge .fdcp-badge-end i,
.fdcp-badge .fdcp-badge-end svg,
.fdcp-badge .fdcp-badge-end img {
  width: 1rem;
  height: 1rem;
}
.fdcp-badge .fdcp-badge-start button,
.fdcp-badge .fdcp-badge-end button {
  all: unset;
  cursor: pointer;
  border-radius: 50%;
  padding: 0.25rem;
  line-height: 0;
}
.fdcp-badge .fdcp-badge-content {
  display: inline-block;
}
.fdcp-badge.fdcp-badge-primary {
  background-color: #333333;
  color: #fff;
  border: 1px solid #333333;
}
.fdcp-badge.fdcp-badge-primary.inverted {
  background-color: #ffffff;
  color: #333333;
  border-color: #333333;
}
.fdcp-badge.fdcp-badge-secondary {
  background-color: #43474E;
  color: #fff;
  border: 1px solid #43474E;
}
.fdcp-badge.fdcp-badge-secondary.inverted {
  background-color: #ffffff;
  color: #43474E;
  border-color: #43474E;
}
.fdcp-badge.fdcp-badge-success {
  background-color: #03662a;
  color: #fff;
  border: 1px solid #03662a;
}
.fdcp-badge.fdcp-badge-success.inverted {
  background-color: #ffffff;
  color: #03662a;
  border-color: #03662a;
}
.fdcp-badge.fdcp-badge-danger {
  background-color: #d3080c;
  color: #fff;
  border: 1px solid #d3080c;
}
.fdcp-badge.fdcp-badge-danger.inverted {
  background-color: #ffffff;
  color: #d3080c;
  border-color: #d3080c;
}
.fdcp-badge.fdcp-badge-warning {
  background-color: #b3800f;
  color: #000;
  border: 1px solid #b3800f;
}
.fdcp-badge.fdcp-badge-warning.inverted {
  background-color: #ffffff;
  color: #b3800f;
  border-color: #b3800f;
}
.fdcp-badge.fdcp-badge-info {
  background-color: #1e7b96;
  color: #fff;
  border: 1px solid #1e7b96;
}
.fdcp-badge.fdcp-badge-info.inverted {
  background-color: #ffffff;
  color: #1e7b96;
  border-color: #1e7b96;
}
.fdcp-badge.fdcp-badge-light {
  background-color: #545961;
  color: #ffffff;
  border: 1px solid #545961;
}
.fdcp-badge.fdcp-badge-light.inverted {
  background-color: #ffffff;
  color: #545961;
  border-color: #545961;
}
.fdcp-badge.fdcp-badge-dark {
  background-color: #333333;
  color: #fff;
  border: 1px solid #333333;
}
.fdcp-badge.fdcp-badge-dark.inverted {
  background-color: #ffffff;
  color: #333333;
  border-color: #333333;
}